body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Arial", "微軟正黑體", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #172633;
  font-size: 15px; }

a {
  color: #09d3ac;
  text-decoration: none; }

*:focus {
  outline: none; }

p, ul, li {
  line-height: 1.5; }

input {
  box-sizing: border-box; }

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #f0f0f0;
  overflow-x: hidden; }

.app-logo {
  height: 30vmin; }

.app-header {
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #2a2a2d; }
  .app-header__container {
    display: flex;
    padding: 20px;
    max-width: 1024px;
    width: 100%; }
  .app-header__logo {
    font-size: 22px;
    font-weight: bold; }
  .app-header__nav {
    flex: 1 1;
    flex-wrap: wrap;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 20px;
    font-size: 15px; }
  .app-header__nav-item {
    margin-left: 10px; }
    .app-header__nav-item > a {
      text-decoration: none; }
    .app-header__nav-item--active {
      font-weight: bold;
      color: #16f6cc; }

.app-body {
  display: flex;
  justify-content: center;
  flex: 1;
  padding: 0px 20px 20px 20px; }
  .app-body__container {
    display: block;
    max-width: 1024px;
    width: 100%; }

.tp-radio__label {
  padding-left: 5px; }

.tp-checkbox__label {
  padding-left: 5px; }

.tp-loader {
  padding: 20px 0; }
  .tp-loader__container {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 10px; }
  .tp-loader__item {
    position: absolute;
    top: 2px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #7a83df;
    animation-timing-function: cubic-bezier(0, 1, 1, 0); }
    .tp-loader__item:nth-child(1) {
      left: 1px;
      animation: lds-ellipsis1 0.6s infinite; }
    .tp-loader__item:nth-child(2) {
      left: 1px;
      animation: lds-ellipsis2 0.6s infinite; }
    .tp-loader__item:nth-child(3) {
      left: 16px;
      animation: lds-ellipsis2 0.6s infinite; }
    .tp-loader__item:nth-child(4) {
      left: 29px;
      animation: lds-ellipsis3 0.6s infinite; }

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(0); } }

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0); }
  100% {
    transform: translate(15px, 0); } }

.tp-bundary {
  font-size: 16px;
  border: 1px dashed #990033;
  margin: 15px 0;
  padding: 20px 10px 10px 10px;
  position: relative;
  background: #ffffcc;
  color: black; }
  .tp-bundary__tag {
    color: #990033;
    position: absolute;
    right: 10px;
    font-size: 12px;
    top: 5px; }

.tp-section {
  font-size: 16px;
  background: #cfcfcf;
  padding: 15px;
  margin-top: 20px;
  border-radius: 6px;
  color: black; }

.tp-table {
  border: black 1px solid;
  border-collapse: collapse;
  width: 100%;
  text-align: center; }
  .tp-table__th, .tp-table__tr, .tp-table__td {
    border: black 1px solid;
    padding: 5px; }

.tp-form {
  display: flex;
  flex-direction: column;
  max-width: 300px; }
  .tp-form__row {
    padding: 10px 0;
    display: flex;
    flex-direction: row; }
    .tp-form__row--right {
      flex-direction: row-reverse; }
  .tp-form__label {
    width: 25%;
    font-weight: bold; }
  .tp-form__field {
    flex: 1; }
    .tp-form__field > input {
      width: 100%; }
  .tp-form__error {
    text-align: right;
    color: #f62e2e;
    padding-top: 3px;
    font-size: 13px; }

.tp-count-down-timer {
  font-family: 'Share Tech Mono', monospace;
  color: #daf6ff;
  text-shadow: 0 0 10px #0aafe6, 0 0 10px;
  margin: 10px 0;
  padding: 0 10px;
  border: 1px solid;
  display: table;
  width: auto;
  border-radius: 5px;
  background: #172633; }
  .tp-count-down-timer__time {
    letter-spacing: 0.05em;
    font-size: 60px;
    padding: 5px 0; }

.tp-tabs {
  border: 1px solid #ddd;
  border-radius: 4px; }

.tp-desc {
  font-size: 15px; }

.tp-hint {
  padding-top: 5px;
  font-size: 13px;
  line-height: 1.5;
  color: #1a3799; }

.tp-tag {
  padding: 0 5px;
  margin: 0 3px;
  border-radius: 2px;
  border: 1px solid #6d6d6d;
  display: inline-block; }

.tp-code {
  padding: 5px;
  font-size: 10px;
  border-radius: 2px;
  border: 1px dashed; }
